.focused,
*:focus,
input:focus + label {
	outline-style: none;

	.accessibility.no-touch & {
		outline: @focus-outline;
	}
}

// Accessibility fix for narrative icons moving when focused on touch devices
// ==================================================
html.accessibility {
	&.touch {
		.narrative-control-left:focus, .narrative-control-left.focus {
			left: 0;

			.dir-rtl & {
				left: inherit;
				right: 0;
			}
		}

		.narrative-control-right:focus, .narrative-control-right.focus {
			right: 0;

			.dir-rtl & {
				right: inherit;
				left: 0;
			}
		}
	}
}
