// ====================================================================================================
// Responsive Settings
// ====================================================================================================

// Device widths
// ==================================================
@device-width-small: unit(@adapt-device-small, px);
@device-width-medium: unit(@adapt-device-medium, px);
@device-width-large: unit(@adapt-device-large, px);

// Max-widths
// ==================================================
@max-width: 1400px;
@ie8-max-width: 1024px;

// Responsive mixin
// ==================================================
// This mixin is for setting the respovive widths in the theme.
// It allows images size to be reduced and simplified. Only two sizes required 900px / 540px.

.theme-responsive-sizes() {
  margin: auto;
  max-width: @device-width-large;
  padding-left: 0;
  padding-right: 0;

  @media (max-width: (@device-width-large + 20px)) {
    max-width: @device-width-medium;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (max-width: @device-width-medium) {
    max-width: @device-width-small;
  }

  @media (max-width: @device-width-small) {
    max-width: 100%;
  }

  @media (max-width: 321px) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

