// ====================================================================================================
// UI Button Hover Styles
// ====================================================================================================

// Navigation icons
// ==================================================
.navigation-icon-rollover-style(@rollover-style: default) {
  .navigation-icon-rollover-args();

  // Default
  // ==================================================
  .navigation-icon-rollover-args() when (@rollover-style = default) {
    .ui-button-color-hover(color, 0%);
  }

  // Theme
  // ==================================================
  .navigation-icon-rollover-args() when (@rollover-style = theme) {
    .ui-button-color-hover(background-color, 0%);
    .transition-background-color;
  }
}

// Navigation PLP
// ==================================================
.navigation-plp-rollover-style(@rollover-style: default) {
  .navigation-plp-rollover-args();

  // Default
  // ==================================================
  .navigation-plp-rollover-args() when (@rollover-style = default) {
    .page-level-progress-navigation-completion {
      .navigation-color-hover(background-color, 0%);
      .ui-button-color-hover(border, 2px, solid, 0%);
    }

    .page-level-progress-navigation-bar {
      .ui-button-color-hover(background-color, 0%);
    }

    .page-level-progress-plus-icon {
      .ui-button-color-hover(color, 0%);
    }
  }

  // Theme
  // ==================================================
  .navigation-plp-rollover-args() when (@rollover-style = theme) {
    .ui-button-color-hover(background-color, 0%);
    .transition-background-color;

    .page-level-progress-navigation-completion {
      border: 2px solid @navigation-color-inverted;
    }

    .page-level-progress-navigation-bar {
      .primary-color-hover(background-color, 0%);
    }
  }
}

// Drawer close button
// ==================================================
.drawer-close-button-rollover-style(@rollover-style: default) {
  .close-button-default {
    .icon {
      color: @drawer-color-inverted;
      padding: @navigation-padding;

      @media (max-width: (@device-width-small - 1px)) {
        padding: @navigation-padding-mobile;
      }
    }
  }

  .close-button-theme {
    .icon {
      color: @drawer-color-inverted;
      border: 2px solid @drawer-color-inverted;
      border-radius: 50%;
      display: inline-block;

      margin: @navigation-padding - 7px;
      padding: 5px;
    }
  }

  .close-button-rollover-args();

  // Default
  // ==================================================
  .close-button-rollover-args() when (@rollover-style = default) {
    .close-button-default;

    .no-touch &:hover {
      .icon {
        .ui-button-color-hover(color, 0%);
        .transition-color;
      }
    }
  }

  // Theme
  // ==================================================
  .close-button-rollover-args() when (@rollover-style = theme) {
    .close-button-theme;

    .no-touch &:hover {
      .drawer-color-hover(background-color, 0%);
      .transition-background-color;

      .icon {
        .ui-button-color-hover(background-color, 0%);
        .transition-background-color;
      }
    }
  }
}

// Hotgraphic Buttons
// ==================================================
.hotgraphic-popup-button-rollover-style(@rollover-style: default) {
  .close-button-default {
  }

  .close-button-theme {
    .hotgraphic-popup-done {
      top: -2px;

      .hotgraphic-popup-close {
        border: 2px solid @components-color-inverted;
        border-radius: 50%;
      }
    }
  }

  .args();

  // Default
  // ==================================================
  .args() when (@rollover-style = default) {
    .close-button-default;

    // Left-right buttons
    // ==================================================
    .hotgraphic-popup-controls:not(.disabled) {
      .no-touch &:hover {
        .hotgraphic-popup-arrow-l,
        .hotgraphic-popup-arrow-r {
          .ui-button-color-hover(color, 0%);
          .transition-color;
        }
      }
    }

    // Close button
    // ==================================================
    .hotgraphic-popup-done {
      .no-touch &:hover {
        .hotgraphic-popup-close {
          .ui-button-color-hover(color, 0%);
          .transition-color;
        }
      }
    }
  }

  // Theme
  // ==================================================
  .args() when (@rollover-style = theme) {
    .close-button-theme;

    // Left-right buttons
    // ==================================================
    .hotgraphic-popup-controls:not(.disabled) {
      .no-touch &:hover {
        .ui-button-color-hover(background-color, 0%);
        .transition-background-color;

        .hotgraphic-popup-arrow-l,
        .hotgraphic-popup-arrow-r {
          color: @components-color-inverted;
          .transition-color;
        }
      }
    }

    // Close button
    // ==================================================
    .hotgraphic-popup-done {
      .no-touch &:hover {
        .hotgraphic-popup-close {
          .ui-button-color-hover(background-color, 0%);
          color: @components-color-inverted;
          .transition-all-colors;
        }
      }
    }
  }
}

// ====================================================================================================
// Core Animations
// ====================================================================================================

// Timings
// ==================================================
@duration: 0.2s; // for consistency with vanilla
@colorGlobalDuration: 0.2s;
@widthGlobalDuration: 0.6s;
@allGlobalDuration: 0.6s;

// Core colour transitions
// ==================================================
.transition-all-colors (@duration: @colorGlobalDuration) {
  -webkit-transition: background-color @duration ease-in,
    color @duration ease-in, border-color @duration ease-in;
  -moz-transition: background-color @duration ease-in, color @duration ease-in,
    border-color @duration ease-in;
  -ms-transition: background-color @duration ease-in, color @duration ease-in,
    border-color @duration ease-in;
  transition: background-color @duration ease-in, color @duration ease-in,
    border-color @duration ease-in;
}

.transition-background-color (@duration: @colorGlobalDuration) {
  -webkit-transition: background-color @duration ease-in;
  -moz-transition: background-color @duration ease-in;
  -ms-transition: background-color @duration ease-in;
  transition: background-color @duration ease-in;
}

.transition-border-color (@duration: @colorGlobalDuration) {
  -webkit-transition: border-color @duration ease-in;
  -moz-transition: border-color @duration ease-in;
  -ms-transition: border-color @duration ease-in;
  transition: border-color @duration ease-in;
}

.transition-color (@duration: @colorGlobalDuration) {
  -webkit-transition: color @duration ease-in;
  -moz-transition: color @duration ease-in;
  -ms-transition: color @duration ease-in;
  transition: color @duration ease-in;
}

// Core object transitions
// ==================================================
.transition-all (@duration: @allGlobalDuration) {
  -webkit-transition: all @duration ease-in;
  -moz-transition: all @duration ease-in;
  -ms-transition: all @duration ease-in;
  transition: all @duration ease-in;
}

.transition-width (@duration: @widthGlobalDuration) {
  -webkit-transition: width @duration ease-in;
  -moz-transition: width @duration ease-in;
  -ms-transition: width @duration ease-in;
  transition: width @duration ease-in;
}

// Core effects
// ==================================================

// Pulse - used in page-level-progress.less
// ==================================================
@-webkit-keyframes pulse {
  from {
  }
  to {
    background-color: rgba(255, 255, 255, 1);
  }
}

@keyframes pulse {
  from {
  }
  to {
    background-color: rgba(255, 255, 255, 1);
  }
}

.effect-pulse (@delay: @widthGlobalDuration) {
  animation-name: pulse;
  animation-duration: 0.6s;
  animation-delay: @widthGlobalDuration;
  animation-iteration-count: 6;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
}

// scalePulse - used in mcq.less and gmcq.less
// ==================================================
@-webkit-keyframes scalePulse {
  0% {
    -webkit-transform: none;
  }
  50% {
    -webkit-transform: scale(1.4, 1.4);
  }
  100% {
    -webkit-transform: none;
  }
}

@keyframes scalePulse {
  0% {
    transform: none;
  }
  50% {
    transform: scale(1.4, 1.4);
  }
  100% {
    transform: none;
  }
}

.effect-scalePulse () {
  animation-name: scalePulse;
  animation-duration: 0.6s;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
}

// bounceDownTwice - used in page.less
// ==================================================
@-webkit-keyframes bounceDownTwice {
  0% {
    -webkit-transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(5px);
  }
}

@keyframes bounceDownTwice {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(5px);
  }
}

.effect-bounceDownTwice () {
  animation-name: bounceDownTwice;
  animation-duration: 0.25s;
  animation-iteration-count: 4;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
}

// bounceLeftTwice - used in navigation.less
// ==================================================
@-webkit-keyframes bounceLeftTwice {
  0% {
    -webkit-transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(-5px);
  }
}

@keyframes bounceLeftTwice {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-5px);
  }
}

.effect-bounceLeftTwice () {
  animation-name: bounceLeftTwice;
  animation-duration: 0.25s;
  animation-iteration-count: 4;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
}

// bounceLeftOnce - used in articleBlockSlider.less, narrative.less, and quicknav.less
// ==================================================
@-webkit-keyframes bounceLeftOnce {
  0% {
    -webkit-transform: translateX(0px);
  }
  100% {
