// ====================================================================================================
// Heading Styles
// ====================================================================================================

// Magazine style headings - add to theme JSON to apply
// ==================================================
// "_theme": {
// 	"_themePalette": "palette-01",
// 	"_headings": {
// 		"_isEnabled": true,
// 		"_pageHeading": "magazine",
// 		"_articleHeading": "magazine",
// 		"_blockHeading": "magazine",
// 		"_componentHeading": "magazine"
// 	}
// }

// Magazine headings
// ==================================================
@mag-heading-padding: 10px;

.magazine {
  .page-title-inner,
  .article-title-inner,
  .block-title-inner,
  .component-title-inner {
    line-height: 140% !important;
    margin: 0 !important;
    overflow: hidden;
    padding: (@mag-heading-padding / 2) 0 !important;
    padding-left: (@mag-heading-padding * 2) !important;
    position: relative;
  }

  .page-title-inner > span,
  .article-title-inner > span,
  .block-title-inner > span,
  .component-title-inner > span {
    .headings-color(background-color);
    color: @headings-color-inverted;
    padding: (@mag-heading-padding * 1.2) 0 !important;
    line-height: 140% !important;
  }

  .page-title-inner span span,
  .article-title-inner span span,
  .block-title-inner span span,
  .component-title-inner span span {
    position: relative;
    left: -@mag-heading-padding !important;
    z-index: 2;
  }

  .page-title-inner:before,
  .article-title-inner:before,
  .block-title-inner:before,
  .component-title-inner:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: (@mag-heading-padding * 2);
    z-index: 1;
    .headings-color(background-color);
  }
}
